.frameChild {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--misc-white);
  width: 100%;
  height: 100%;
}
.ppxocMilkywayDress {
  width: 269px;
  height: 22px;
  position: relative;
  text-transform: uppercase;
  display: inline-block;
  color: var(--text-text);
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 0;
}
.exploreThisSkill {
  width: 125px;
  position: relative;
  display: none;
}
.signIn {
  text-decoration: underline;
}
.signInOrContainer {
  align-self: stretch;
  position: relative;
}
.signInOrCreateAnAccountTWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-6xs) 0 0;
}
.vuesaxlinearheartIcon {
  height: 24px;
  width: 24px;
  position: relative;
  min-height: 24px;
}
.frameParent,
.ppxocMilkywayDressInPresseParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.frameParent {
  align-self: stretch;
  flex: 1;
  flex-direction: row;
  gap: var(--gap-6xl);
  font-size: var(--font-size-sm);
}
.ppxocMilkywayDressInPresseParent {
  position: absolute;
  top: 16px;
  left: 0;
  width: 100%;
  height: 47px;
  flex-direction: column;
  gap: var(--gap-12xs);
  z-index: 1;
}
.rectangleParent {
  align-self: stretch;
  height: 63px;
  position: relative;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--text-secondary-text);
  font-family: var(--font-simplon-norm);
}
